<template>
<div>
	<h3 class="base-title">基础用法</h3>
	<p class="base-content"></p>

	<div class="demo-block" :class="{'show-case-core':caseObj1.showCoreFlag}">
		<div class="demo-block-source">
			<bh-switch v-model="value1" active-color="#13ce66" inactive-color="#ff4949">
			</bh-switch>
		</div>
		<div class="demo-block-core">
			<div class="demo-block-intro">绑定v-model到一个Boolean类型的变量。可以使用active-color属性与inactive-color属性来设置开关的背景色。</div>
