<script>
const caseSourceVal = `
<bh-switch v-model="value1" active-color="#13ce66" inactive-color="#ff4949">
</bh-switch>


export default {
	data () {
		return {
			value1: true,
		};
	},
}
`;
export default {
	data () {
		return {
			caseObj1: {
				showCoreFlag: false,
				coreUrl: 'https://codepen.io/xiebaolin/pen/VwzLpBp',
			},
			caseSourceVal: caseSourceVal,

			value1: true,
			value2: false,
		};
	},
	methods: {
		// 切换代码显隐
		toggleDemoCore (item) {
			item.showCoreFlag = !item.showCoreFlag;
		},
	},
}
